.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .checkoutProduct-image {
    object-fit: contain;
    width: 180px;
    height: 180px;
  }
  .checkoutProduct-info {
    padding-left: 20px;
  }
  .checkoutProduct-info > button {
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
    width: 150px;
    height: 35px;
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  .checkoutProduct-info > button:active {
    transform: scale(0.8);
  }
  .checkoutProduct-rating {
    display: flex;
  }
  .checkoutProduct-rating p {
    width: 20px;
    margin: 0 5px;
  }
  .checkoutProduct-rating p img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .checkoutProduct-title {
    font-size: 17px;
    font-weight: 800;
  }