.product {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: column;
    z-index: 1;
    width: 100%;
    max-height: 500px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    background-color: white;
    transition: background 0.3s ease;
  }
  .product:hover {
    background-color: #131921;
    color: white;
  }
  
  .product > img {
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
    transition: all 0.3s ease;
  }
  .product:hover img {
    transform: scale(1.1);
  }
  .product-info {
    width: 100%;
    min-height: 100px;
  }
  .product-price {
    margin-top: 8px;
    font-size: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  .product-rating {
    display: flex;
  }
  
  .product-rating p {
    width: 20px;
    margin: 0 5px;
  }
  .product-rating p img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  .product > button {
    display: block;
    margin: 10px auto 0;
    background-color: #cd9042;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
    width: 150px;
    height: 35px;
    border-radius: 5px;
    transition: transform 0.3s ease;
  }
  .product > button:active {
    transform: scale(0.8);
  }
  .hom-row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
  }