.payment {
    background-color: white;
  }
  .payment-container > h1 {
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234, 237, 237);
    border-bottom: 1px solid lightgray;
  }
  .payment-section {
    display: flex;
    padding: 20px;
    margin: 20px;
    border-bottom: 1px solid lightgray;
  }
  .payment-title,
  .payment-section > h3 {
    flex: 0.2;
  }
  .payment-address,
  .payment-items,
  .payment-details {
    flex: 0.8;
  }
  .payment-detaills > form {
    max-width: 400px;
  }
  .payment-details .payment-priceContainer > h3 {
    padding-top: 20px;
  }
  .payment-details > form > div > button {
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
  }